import React, { useEffect, useState } from "react";
import { Web3Service } from "../services/web3";
import { Link } from "react-router-dom";
import { NetConfig } from "../utils/config";
import { snarkServiceInterface } from "../services/snark";
import Logo from "../images/logo.png";

export default function Navbar() {
  const [account, setaccount] = useState("");
  const [networkName, setNetworkname] = useState("");
  const [keysDownloading, setKeysDownloading] = useState(false);

  useEffect(() => {
    connectWallet();
  }, []);

  const connectWallet = async () => {
    setKeysDownloading(true);

    const account = await Web3Service.connect();

    setKeysDownloading(false);

    if (!account) {
      alert("Metamask not available");
      return;
    }

    setNetworkname(Web3Service.networkName);
    snarkServiceInterface.initTornadoContract(
      NetConfig[Web3Service.networkName].tornadoAddress
    );
    setaccount(account);
  };

  const getSlicedWalletAddress = (account) => {
    return `${String(account).slice(0, 4)}...${String(account).slice(37)}`;
  };

  return (
    <div>
      <nav
        className="navbar navbar-expand-lg"
        style={{ backgroundColor: "#E7FAFF", height: 70 }}
      >
        <div className="container">
          <img src={Logo} height={25} />

          <div
            className="collapse navbar-collapse justify-content-end"
            id="navbarNavAltMarkup"
          >
            <div className="navbar-nav">
              <a
                className="nav-link text-dark me-4"
                aria-current="page"
                href="#"
              >
                Referral History
              </a>
              <a className="nav-link text-dark me-4" href="#">
                Banned Users
              </a>
              <a className="nav-link text-dark me-4" href="#">
                Mirror Sites
              </a>
            </div>

            {keysDownloading ? (
              <button
                type="button"
                disabled={true}
                className="btn btn-primary rounded-pill"
                style={{ width: 170 }}
              >
                Keys Downloading...
              </button>
            ) : (
              <button
                type="button"
                disabled={account !== ""}
                onClick={connectWallet}
                style={{ width: 170 }}
                className="btn btn-primary rounded-pill"
              >
                {account === ""
                  ? "Connect Wallet"
                  : `${getSlicedWalletAddress(account)}`}
              </button>
            )}
          </div>
        </div>
      </nav>
    </div>
  );
}
