import React, { memo } from "react";
import { Button, Modal, Form } from "react-bootstrap";

function CommitmenModal({ show, handleClose, onCopyHandler, commitment }) {
  return (
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>Commitment</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group className="mb-3" controlId="formBasicCommitment">
            <Form.Label>Commitment</Form.Label>
            <Form.Control type="text" disabled={true} value={commitment} />
            <Form.Text className="text-muted"></Form.Text>
            <Button className="mt-2" variant="primary" onClick={onCopyHandler}>
              Copy
            </Button>
          </Form.Group>
        </Form>
      </Modal.Body>
    </Modal>
  );
}

export default memo(CommitmenModal);
