import React from "react";

function Deposit({ onDepositHandler, loading }) {
  return (
    <div>
      <label className="mb-2">Token</label>
      <select className="form-select" aria-label="network-select" defaultValue="eth">
        <option value="eth">Ethereum</option>
      </select>
      <button className="btn btn-primary mt-4 w-100" onClick={onDepositHandler} disabled={loading}>
        {loading ? <span className="spinner-border spinner-border-sm" role="status"></span> : <span>Deposit</span>}
      </button>
    </div>
  );
}

export default Deposit;
